import React from 'react';
import HomeCss from './../../css/Home.css';

import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";

import Explore from './../Explore';
import HomeMain from './../HomeMain';
import Profile from './../Profile';

export let estouOnde = {}

class Dashboard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      componentRender: HomeMain
    }
  }
  componentDidMount(){
      this.changeComponent()
  }
  changeComponent(){
    setInterval(() => {
        const url = window.location.pathname
        if(url == "/" || url == "/home"){
            this.setState({componentRender: HomeMain})
        } else if(url == "/explore"){
            this.setState({componentRender: Explore})
        } else if(url == "/profile"){
            this.setState({componentRender: Profile})
        }
    }, 200);
  }
  render(){
  return (
    <div className="Dashboard">
      <Left_Bar/>
      <div className="center">
        { <this.state.componentRender/> }
      </div>
    </div>
  );
}
}

class Button extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
          
        }
      }
      render(){
      return (
        <div className={`button ${(this.props.ativo ? 'buttonAtivo' : 'buttonNotAtivo')}`}>
        {
            this.props.news > 0 &&
        <div className={`newsCount`}>
            { 
            this.props.news < 99 && <span> {this.props.news} </span> ||
            this.props.news > 99 && <span> +99 </span>
            }
        </div>
        }
          <i className={`icon ${this.props.icon}`}></i>
          <span className="title">{ this.props.title }</span>
        </div>
      );
    }
}

class Left_Bar extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        logo: 'fab fa-twitter',
        moreItem: false,
        links: [
            {
                'title': 'Home',
                'link': '/home',
                'icon': 'fas fa-home',
                'news': 0,
                'accesible': true
            },
            {
                'title': 'Profile',
                'link': '/profile',
                'icon': 'fas fa-user',
                'news': 0,
                'accesible': true
            },
            {
                'title': 'Comunidades',
                'link': '/servers',
                'icon': 'fas fa-server',
                'news': 0,
                'accesible': true
            },
            {
                'title': 'Explore',
                'link': '/explore',
                'icon': 'fab fa-wpexplorer',
                'news': 0,
                'accesible': true
            },
            {
                'title': 'Chat',
                'link': '/chat',
                'icon': 'fas fa-comments',
                'news': 0,
                'accesible': true
            },
            {
                'title': 'Friends',
                'link': '/friends',
                'icon': 'fas fa-address-book',
                'news': 10,
                'accesible': true
            }
        ],
        linkAtual: {}
      }
    }
    moreInfo(){
        this.setState({moreItem: !this.state.moreItem})
    }
    componentDidMount(){
        const url = window.location.pathname
        const found = this.state.links.find(e => e.link == url)
        if(found){
            const id = this.state.links.indexOf(found)
            document.title = `${ (this.state.links[0].news + this.state.links[1].news + this.state.links[2].news + this.state.links[3].news
                + this.state.links[4].news + this.state.links[5].news > 0 ? this.state.links[0].news + this.state.links[1].news + this.state.links[2].news + this.state.links[3].news
                + this.state.links[4].news + this.state.links[5].news :  "" ) } ${this.state.links[id].title} / Social Network 
            
            `
            this.setState({linkAtual: this.state.links[id]})
            estouOnde = this.state.linkAtual
        } else{
            if(url == "/"){
                document.title = `${ (this.state.links[0].news + this.state.links[1].news + this.state.links[2].news + this.state.links[3].news
                    + this.state.links[4].news + this.state.links[5].news > 0 ? + this.state.links[0].news + this.state.links[1].news + this.state.links[2].news + this.state.links[3].news
                    + this.state.links[4].news + this.state.links[5].news :  "" ) } ${this.state.links[0].title} / Social Network
                 
                `
            this.setState({linkAtual: this.state.links[0]})
            estouOnde = this.state.linkAtual
            }
        }
    }
    acessButton(object){
        if(object.accesible){
            document.title = `${ (this.state.links[0].news + this.state.links[1].news + this.state.links[2].news + this.state.links[3].news
                + this.state.links[4].news + this.state.links[5].news > 0 ? this.state.links[0].news + this.state.links[1].news + this.state.links[2].news + this.state.links[3].news
                + this.state.links[4].news + this.state.links[5].news :  "" ) } ${object.title} / Social Network`
            this.setState({linkAtual: object})
            estouOnde = this.state.linkAtual
        }
    }
    blurMoreItem(){
        this.setState({moreInfo: false})
    }
    render(){
    return (
      <div className="left_bar">  
      <Link to='/home'><h1 className={`logo ${(this.state.linkAtual.link == this.state.links[0].link ? 'logoAtivo': '')}`} 
      onClick={() => this.acessButton(this.state.links[0])}> <i className={this.state.logo}></i>  </h1></Link>
        { this.state.links.map((item, i) =>
        <Link to={item.link}>
        <div onClick={() => this.acessButton(item)}>
            <Button 
            title={item.title}
            icon={item.icon}
            ativo={this.state.linkAtual.link == item.link}
            news={item.news}
            key={i}/>
        </div> </Link>
        ) }
        <div onClick={() => this.moreInfo()}>
            <Button 
            title={"More"}
            icon={"fas fa-ellipsis-h"}/>
        </div>
        {
        this.state.moreItem && 
        <div className="moreItem"
        onFocus={() => this.blurMoreItem()}>
            <li>
                <span className="icon">
                    <i className="fas fa-palette"></i>
                </span>
                <span className="title">Display</span>
            </li>
        </div>
        }
      </div>
    );
  }
  }

export default Dashboard;