import React from 'react';

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      logged: true
    }
  }
  render(){
  return (
    <div className="Home">
      <div className="header">
        <h1>Home</h1>
      </div>
    </div>
  );
}
}

export default Home;