import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import { estouOnde } from './components/Dashboard';
import Dashboard from './components/Dashboard';
import Home from './components/Home';
import Splash from './components/Splash';

import Style from './css/Style.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      logged: true,
      mounted: false,
      darkMode: true
    }
  }
  componentDidMount(){
      const react = this
      window.onload = this.mounted(react);
  }
  mounted(react){
    react.setState({mounted: true})
    console.warn("Version 0.0.1")
  }
  render(){
  return (
    <div className={`App ${(this.state.darkMode ? 'dark' : 'white')}`}>
        <div className={`container ${(this.state.mounted ? 'mounted' : 'isNotMounted')}`}>
        <Switch>
          <Route path="/">
            { 
              (this.state.logged && this.state.mounted && <Dashboard/> || !this.state.mounted && <Splash/> ) ||
              (!this.state.logged && <Home/>)
            }
          </Route>
        </Switch>
      </div>
    </div>
  );
}
}

export default App;